@include media-breakpoint-up(xs) {
	.brand {
		background-image: url('data:image/svg+xml;base64,');
		background-repeat: no-repeat;
	}
	.header-logo {
		display:inline-block;
		height:37px;
		width:175px;
		line-height:1.5;
	}
	.overlay {
		height: 0%;
		width: 100%;
		position: fixed;
		z-index: 100;
		top: 0;
		left: 0;
		background-color: $white;
		overflow-y: hidden;
		transition: 0.5s;
		z-index:1100;
		.overlay-content {
			position: absolute;
			top:0;
			left:0;
			width: 100%;
			height:100vh;
			padding:100px 0;
			overflow:auto;
		}
	}
	.overlay-close {
		position:absolute;
		z-index:1100;
	}
	.overlay-menu {
		position:fixed;
		z-index:1000;
	}
	.overlay-close, .overlay-menu {
		top:25px;
		right:30px;
		padding:0.5rem;
	}
	header {
		.dropdown-menu {
			border:$mdc-light-blue-400 solid 3px;
			box-sizing:border-box;
			margin-top:15px;
			position:relative
			.dropdown-item {
				padding: .3rem 1.5rem;
			}
			.dropdown-item:hover {
				background-color:$mdc-light-blue-100;
			}
			.arrow {
				border-style: solid;
				position: absolute;
				border-color: transparent transparent $mdc-light-blue-400 transparent;
				border-width: 10px;
				top: -20px;
				right: 1.2rem;
			}
			.arrow:after {
				border-style: solid;
				position: absolute;
				border-color: transparent transparent $white transparent;
				border-width: 8px;
				top: -4px;
				right:-8px;
				content: "";
			}
		}
	}
}

@include media-breakpoint-up(sm) {
	.overlay-close, .overlay-menu {
		top:30px;
		right:30px;
	}
}

@include media-breakpoint-up(md) {
	.header-logo {
		height:42px;
		width:200px;
	}
	.overlay-close, .overlay-menu {
		padding:1rem;
	}
}

@include media-breakpoint-up(lg) {

}

@include media-breakpoint-up(xl) {
	.header-logo {
		height:50px;
		width:240px;
	}
}