  border-radius: 2rem;
  padding: .5rem 1.5rem;
  font-weight: 500;
  text-transform: uppercase; }

.btn-primary {
  background-color: #29B6F6;
  border-color: #29B6F6; }

.btn-primary:hover {
  background-color: shade(#29B6F6, 15%);
  border-color: shade(#29B6F6, 15%); }

.btn-secondary {
  background-color: #78909C;
